
  import { defineComponent } from "vue";
import { useStore } from "vuex";

  export default defineComponent({
    props: {
      model: {
        required: true,
        default: {
          id: 0,
          dateAdded: "1/3/2021",
          text: "default task",
          status: false,
        },
      },
      listId: {
        type: Number,
        required: true,
      }
    },
    setup(props) {
      const store = useStore();

      function onToggle() {
        store.commit("TOGGLE_ITEM", {
          listId: props.listId,
          itemId: props.model.id,
        });
      }

      function onDelete() {
        store.commit("DELETE_ITEM", {
          listId: props.listId,
          id: props.model.id,
        });
      }

      function onEdit(event: Event) {
        const target = event.target as HTMLElement;
        store.commit("EDIT_ITEM", {
          listId: props.listId,
          id: props.model.id,
          text: target.innerText,
        });
        target.blur();
      }

      return {
        onToggle,
        onDelete,
        onEdit,
      };
    },
  });
